@import "../../../sass/consts";

.museum_subtypes_page {
  background-color: $bg8;

  .type {
    margin-bottom: .5rem;
    background-color: white;
    padding: .5rem;

    span {
      color: $bg4;
    }

    .checkbox_img {
      width: 1.5rem;
      margin-right: .3rem;
    }

    .logo {
      width: 100%;
      border-radius: .5rem .5rem 0 0;
    }

    .infos {
      background-color: $bg8;
      border-radius: .5rem;
      padding: .5rem;
    }
  }

  .oprs {
    padding: .3rem .5rem;
    justify-content: flex-end;
    background-color: white;

    .am-flexbox-item {
      display: flex;
      justify-content: space-around;

      span {
        padding: 0 .5rem;
        color: $bg4;
      }
    }

    img {
      margin-left: .5rem;
      width: 1.5rem;
    }
  }
}
